.socialPart{

    display: flex;
    flex-direction: column;
    justify-content: center;
    position: fixed;
    top: 50px;
    right: 0;
    margin: 20px 0;
    background-color: white;
}
.socialBlock{
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 8px;
    border: rgba(0, 0, 0, 0) solid 2px;
    border-radius: 2px;
    transition: border .3s ease;
    cursor: pointer;
}
.socialLink{
    text-decoration: none;
}
.socialBlock:last-child{
    margin-right: 0;
}
/*.socialBlock:hover{
    border: black solid 2px;
    transition: border .3s ease;
}*/
.socialLogo{
    width: 50px;
}


@media (max-width: 481px) {
    .socialPart{
        top: 30px;
    }
    .socialLogo{
        width: 30px;
    }.socialBlock{
         font-size: 12px;
     }
}