.repertoire{
    display: flex;
    flex-direction: column;
    align-items: center;

}
.songsPalette{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
.palette{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.palette:nth-child(1){
    margin-right: 90px;
}
.songs{
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    border: 2px #bbbbbb solid;
    width: 480px;
}
.song{
    width: 200px;
    height: 200px;
    margin: 20px;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    background-color: #ffead9;
    transition: 0.2s;
}
.song:hover{
    transform: scale(1.05);
    background-color: #F2C6A0;
}
.songTitle{
    padding: 15px;
    z-index: 2;
    color: white;
    font-size: 22px;
    text-shadow: 1px 1px 2px #000000, 0 0 1em #000000, 0 0 0.5em #000000;
}
.alb{
    position: absolute;
    width: 200px;
    height: 200px;
    top: 0;
    left: 0;
    filter: blur(2px);
}

@media (max-width: 992px) {
    .songs{
        padding: 0;
        width: 720px;
    }
}
@media (max-width: 481px) {
    .songsPalette{
        flex-direction: column;
    }
    .palette:nth-child(1){
        margin-right: 0;
    }
    .songs{
        padding: 0;
        width: 360px;
    }
    .song{
        width: 100px;
        height: 100px;
        margin: 10px;
    }
    .songTitle{
        font-size: 13px;
    }
    .alb{
        width: 100px;
        height: 100px;
    }
}
